import { theme } from "@rp/react/theme"
import { IconButton, InputAdornment, InputLabel, OutlinedInput } from "@rp/react/web/mui/core"
import { VisibilityOffOutlined, VisibilityOutlined } from "@rp/react/web/mui/icons"

type Props = {
  showPassword: boolean
  handleClickShowPassword: () => void
  handleMouseDownPassword: (event: React.MouseEvent<HTMLButtonElement>) => void
} & React.ComponentProps<typeof OutlinedInput>

export const LoginPasswordField = ({
  showPassword,
  handleClickShowPassword,
  handleMouseDownPassword,
  label,
  sx,
  ...props
}: Props) => {
  return (
    <>
      <InputLabel htmlFor="password">{label}</InputLabel>
      <OutlinedInput
        id="password"
        required
        type={showPassword ? "text" : "password"}
        sx={{ padding: "0px", ...sx }}
        endAdornment={
          <InputAdornment sx={{ position: "absolute", right: 16 }} position="end">
            <IconButton
              aria-label="toggle visibility"
              onClick={handleClickShowPassword}
              onMouseDown={handleMouseDownPassword}
              edge="end"
            >
              {showPassword ? (
                <VisibilityOffOutlined sx={{ color: theme.palette.grey[300] }} />
              ) : (
                <VisibilityOutlined sx={{ color: theme.palette.grey[300] }} />
              )}
            </IconButton>
          </InputAdornment>
        }
        {...props}
      />
    </>
  )
}
